@import url('https://fonts.googleapis.com/css?family=Open+Sans|Open+Sans+Condensed:300,700');
body {
  font-family: "Open Sans", Helvetica, sans-serif;
  text-align: center;
  margin: 20px;
}

img {
  max-height: 150px;
  margin: auto;
  display: inline-block;
}
h1, h2, h3, h4, h5 {
  margin: 0;
  font-weight: 400;
}


.cardtwo {
  display: inline-block;
  width: 350px;
  padding: 1em;
  border-radius: 15px;  
  margin: 10px;
  background: #ddd;
  text-align: left;
  box-shadow: 0px 5px 20px -10px #111111;
  position: relative;  
  transition: 0.4s;

  /* @media (max-width: 767px) {
    width: 300px;
  } */

}

.heading {
    text-align: right;
    /* media queries */
    @media (max-width: 767px) {
      text-align: center;
    }
    @media (max-width: 400px) {
      text-align: left;
    }
}
  

.card__caption {
    /* background-color: rgba(#ffffff, 0.65); */
    background-color: rgba(255, 255, 255, 0.65);
    padding: 1em;
    position: relative;
    border-radius: 0 0 3px 3px;
}


.card__image-container {
    background-color: rgba(#000000, 0.7);
    /* justify-content: center;
    align-items: center; */
    text-align: center;
    padding: 1em 1em 0;
    border-radius: 3px 3px 0 0;
}
  
.card__type {
    position: absolute;
    top: 0;
    right: 1em;
    transform: translateY(-50%);
    color: #ffffff;
    text-transform: uppercase;
    font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
    letter-spacing: 0.1em;
    padding: 0.25em;
    line-height: 1;
    border-radius: 2px;
    background: #bbbbbb;
}

.card__label {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 400;
    display: block;
    margin-bottom: 3px;
}
  
  
.card__name {
    font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
    text-align: center;
    font-size: 1.5em;    
    font-weight: 700;
    letter-spacing: 0.02em;
}
  
.card__stats {
    margin: 1em 0;
    width: 100%;
}

.card__stats th {
    font-family: "Open Sans Condensed", "Open Sans", helvetica, sans-serif;
    text-align: right;
    font-weight: 300;
}

.card__stats th, td {
    width: 50%;
    padding: 0.25em 0.5em 0;
}
  
.card__abilities {
    display: flex;
    justify-content: space-between;
}

.card__ability {
    margin-top: 1em;
    flex: 1 0;
}
    
.card--normal {
    background: linear-gradient(110deg, #FDBB2D 0%, #3A1C71 100%);
    box-shadow: 0px 5px 20px -10px #3A1C71;
}
